import React from 'react';
import { useParams } from 'react-router-dom';
import { Box, Typography, Paper, Divider, Chip, Stack } from '@mui/material';
import BookmarkButton from '../components/BookmarkButton';

//      
const getSchoolColor = (school) => {
    switch (school) {
        case '':
            return 'primary'; // 
        case '':
            return 'secondary'; // 
        case '':
            return 'success'; // 
        case '':
            return 'error'; // 
        case '':
            return 'warning'; // 
        case '':
            return 'info'; // 
        case '':
            return 'default'; // 
        default:
            return 'default'; //  
    }
};

const SpellDetailPage = ({ spells }) => {
    const { spellName } = useParams(); //     URL
    const spell = spells.find(spell => spell.name === spellName); //    

    if (!spell) {
        return <Typography variant="h4">  </Typography>;
    }

    return (
        <Box sx={{ padding: 3, maxWidth: 800, margin: '0 auto' }}>
            <Paper elevation={3} sx={{ padding: 3 }}>
                {/*     */}
                <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                    <Typography variant="h4" sx={{ fontWeight: 'bold' }}>
                        {spell.name}
                    </Typography>
                    <BookmarkButton spell={spell} />
                </Box>

                {/*   */}
                <Stack direction="row" spacing={2} sx={{ mt: 2 }}>
                    <Chip label={`: ${spell.school}`} color={getSchoolColor(spell.school)} />
                    <Chip label={`: ${spell.level}`} color="secondary" />
                </Stack>

                <Divider sx={{ my: 3 }} />

                {/*   */}
                <Typography variant="body1" sx={{ mb: 2 }}>
                    {spell.text}
                </Typography>

                <Divider sx={{ my: 3 }} />

                {/*   */}
                <Box>
                    <Typography variant="h6" sx={{ fontWeight: 'bold', mb: 2 }}>
                         
                    </Typography>
                    <Typography variant="body2" sx={{ mb: 1 }}>
                        <strong> :</strong> {spell.castingTime}
                    </Typography>
                    <Typography variant="body2" sx={{ mb: 1 }}>
                        <strong>:</strong> {spell.range}
                    </Typography>
                    <Typography variant="body2" sx={{ mb: 1 }}>
                        <strong>:</strong> {spell.components}
                    </Typography>
                    <Typography variant="body2" sx={{ mb: 1 }}>
                        <strong>:</strong> {spell.duration}
                    </Typography>
                    <Typography variant="body2" sx={{ mb: 1 }}>
                        <strong>:</strong> {spell.spellClass.map(cls => cls.name).join(', ')}
                    </Typography>
                </Box>

                <Divider sx={{ my: 3 }} />

                {/*   ( ) */}
                {spell.additionalInfo && (
                    <Box>
                        <Typography variant="h6" sx={{ fontWeight: 'bold', mb: 2 }}>
                             
                        </Typography>
                        <Typography variant="body2">{spell.additionalInfo}</Typography>
                    </Box>
                )}
            </Paper>
        </Box>
    );
};

export default SpellDetailPage;