import React from 'react';
import { useParams } from 'react-router-dom';
import { Box, Typography, Paper, Divider, Chip, CardMedia } from '@mui/material';

//      
const getTypeColor = (type) => {
    switch (type) {
        case '':
            return 'primary'; // 
        case '':
            return 'secondary'; // 
        case '':
            return 'error'; // 
        case '':
            return 'warning'; // 
        case '':
            return 'info'; // 
        default:
            return 'default'; // 
    }
};

const MonsterDetailPage = ({ monsters }) => {
    const { monsterName } = useParams(); //     URL
    const monster = monsters.find(monster => monster.name === monsterName); //    

    if (!monster) {
        return <Typography variant="h4">  </Typography>;
    }

    return (
        <Box sx={{ padding: 3, maxWidth: 800, margin: '0 auto' }}>
            <Paper elevation={3} sx={{ padding: 3 }}>



                <Box sx={{ overflow: 'hidden', position: 'relative' }}>
                    {/*    */}
                    <Typography variant="h4" sx={{ fontWeight: 'bold', mb: 2 }}>
                        {monster.name}
                    </Typography>

                    {/*  "" */}
                    <Chip
                        label={monster.type} color={getTypeColor(monster.type)}
                        sx={{ position: 'absolute', top: 10, right: 10 }}
                    />
                </Box>
                <CardMedia
                    component="img"
                    src={`${process.env.PUBLIC_URL}/static/image/monsters/` + monster.imgStaticUrl}
                    alt={monster.name}
                    sx={{ width: '100%', height: '100%', objectFit: 'contain' }}
                />
                <Divider sx={{ my: 3 }} />

                {/*   */}
                <Typography variant="body1" sx={{ mb: 2 }}>
                    <strong>:</strong> {monster.size}
                </Typography>
                <Typography variant="body1" sx={{ mb: 2 }}>
                    <strong>  (CR):</strong> {monster.cr}
                </Typography>
                <Typography variant="body1" sx={{ mb: 2 }}>
                    <strong>:</strong> {monster.hp}
                </Typography>
                <Typography variant="body1" sx={{ mb: 2 }}>
                    <strong>:</strong> {monster.speed}
                </Typography>

                <Divider sx={{ my: 3 }} />

                {/*   HTML- */}
                {monster.fiction && (
                    <Box sx={{ mb: 2 }}>
                        <Typography variant="h6" sx={{ fontWeight: 'bold', mb: 2 }}>
                            
                        </Typography>
                        <div
                            dangerouslySetInnerHTML={{ __html: monster.fiction }}
                            style={{ lineHeight: monster.lineHeight || '1.5' }}
                        />
                    </Box>
                )}

                <Divider sx={{ my: 3 }} />

                {/*    */}
                <Typography variant="h6" sx={{ fontWeight: 'bold', mb: 2 }}>
                    
                </Typography>
                {monster.monsterTrait?.map((trait, index) => (
                    <Box key={index} sx={{ mb: 2 }}>
                        <div dangerouslySetInnerHTML={{ __html: trait.name }} />
                        <div dangerouslySetInnerHTML={{ __html: trait.text }} />
                    </Box>
                ))}
                <Divider sx={{ my: 3 }} />
                <Typography variant="h6" sx={{ fontWeight: 'bold', mb: 2 }}>
                    
                </Typography>
                {monster.monsterAction?.map((action, index) => (
                    <Box key={index} sx={{ mb: 2 }}>
                        <div dangerouslySetInnerHTML={{ __html: action.name }} />
                        <div dangerouslySetInnerHTML={{ __html: action.text }} />
                    </Box>
                ))}
            </Paper>
        </Box>
    );
};

export default MonsterDetailPage;