import React from 'react';
import { Box, FormControl, InputLabel, Select, MenuItem } from '@mui/material';

const MonsterFilter = ({ filters, setFilters }) => {
    //  
    const monsterTypes = ['', '', '', '', ''];

    //   (CR)
    const challengeRatings = ['0', '1/8', '1/4', '1/2', '1', '2', '3', '4', '5'];

    // 
    const bioms = ['', '', '', ''];

    const handleChange = (event) => {
        const { name, value } = event.target;
        setFilters({ ...filters, [name]: value });
    };

    return (
        <Box sx={{ display: 'flex', gap: 2, mb: 3 }}>
            {/*    */}
            <FormControl fullWidth size="small">
                <InputLabel></InputLabel>
                <Select
                    name="type"
                    value={filters.type || ''}
                    onChange={handleChange}
                    label=""
                >
                    <MenuItem value=""> </MenuItem>
                    {monsterTypes.map((type, index) => (
                        <MenuItem key={index} value={type}>
                            {type}
                        </MenuItem>
                    ))}
                </Select>
            </FormControl>

            {/*     (CR) */}
            <FormControl fullWidth size="small">
                <InputLabel>  (CR)</InputLabel>
                <Select
                    name="cr"
                    value={filters.cr || ''}
                    onChange={handleChange}
                    label="  (CR)"
                >
                    <MenuItem value=""> </MenuItem>
                    {challengeRatings.map((cr, index) => (
                        <MenuItem key={index} value={cr}>
                            {cr}
                        </MenuItem>
                    ))}
                </Select>
            </FormControl>

            {/*    */}
            <FormControl fullWidth size="small">
                <InputLabel></InputLabel>
                <Select
                    name="biom"
                    value={filters.biom || ''}
                    onChange={handleChange}
                    label=""
                >
                    <MenuItem value=""> </MenuItem>
                    {bioms.map((biom, index) => (
                        <MenuItem key={index} value={biom}>
                            {biom}
                        </MenuItem>
                    ))}
                </Select>
            </FormControl>
        </Box>
    );
};

export default MonsterFilter;