import React from 'react';
import { Card, CardContent, CardHeader, Typography, Grid2, Paper, Divider, Chip, Stack, Box } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import BookmarkButton from '../components/BookmarkButton';


//      
const getSchoolColor = (school) => {
    switch (school) {
        case '':
            return 'primary'; // 
        case '':
            return 'secondary'; // 
        case '':
            return 'success'; // 
        case '':
            return 'error'; // 
        case '':
            return 'warning'; // 
        case '':
            return 'info'; // 
        case '':
            return 'default'; // 
        default:
            return 'default'; //  
    }
};

const SpellCards = ({ spells }) => {
    const navigate = useNavigate();

    //    
    const handleCardClick = (spellName) => {
        navigate(`/spells/${spellName}`);
    };

    return (
        <div>
            <Grid2 container spacing={3} justifyContent="center">
                {spells.map((spell, index) => {
                    const schoolColor = getSchoolColor(spell.school); //   

                    return (<Grid2 item key={index} xs={12} sm={6} md={4}>
                        <Paper
                            elevation={3}
                            sx={{
                                cursor: 'pointer',
                                transition: 'transform 0.2s, box-shadow 0.2s',
                                '&:hover': {
                                    transform: 'scale(1.05)',
                                    boxShadow: '0px 4px 20px rgba(0, 0, 0, 0.2)',
                                },
                            }}

                        >
                            <Card sx={{ height: '100%' }}>
                                <Box sx={{ width: 300, height: 300, overflow: 'hidden', position: 'relative' }}>
                                <CardHeader

                                    title={spell.name}
                                    action={
                                        <BookmarkButton spell={spell} />
                                    }
                                />
                                <CardContent onClick={() => handleCardClick(spell.name)}>
                                    {/*    */}
                                    {/*   */}
                                    <Stack direction="row" spacing={2} sx={{ mt: 2 }}>
                                        <Chip label={`: ${spell.school}`} color={schoolColor} />
                                        <Chip label={`: ${spell.level}`} color="secondary" />
                                    </Stack>
                                    <Divider sx={{ my: 2 }} />

                                    {/*   */}
                                    <Typography variant="body2" sx={{ mb: 1 }}>
                                        <strong>:</strong> {spell.level}
                                    </Typography>
                                    <Typography variant="body2" sx={{ mb: 1 }}>
                                        <strong> :</strong> {spell.castingTime}
                                    </Typography>
                                    <Typography variant="body2" sx={{ mb: 1 }}>
                                        <strong>:</strong> {spell.range}
                                    </Typography>
                                    <Typography variant="body2" sx={{ mb: 1 }}>
                                        <strong>:</strong> {spell.components}
                                    </Typography>

                                    <Divider sx={{ my: 2 }} />

                                    {/*  */}
                                    <Typography variant="body2">
                                        <strong>:</strong> {spell.spellClass.map(cls => cls.name).join(', ')}
                                    </Typography>
                                </CardContent>
                                </Box>
                            </Card>
                        </Paper>
                    </Grid2>)
                })}
            </Grid2>
            
        </div>
    );
};

export default SpellCards;